export const createPost=(postData)=> dispatch=> {
    console.log('action called')
    fetch('https://jsonplaceholder.typicode.com/todos',{
        method:'POST',
        headers:{
            'content-type':'application/json'
        },
        body:JSON.stringify(postData)
    })
        .then(res => res.json())
        .then(post=>dispatch({
            type:NEW_POST,
            payload:post
        }))
}

